<template>
    <div class="address">
        <section class="mine-datas">
            <!--  -->
            <section class="name">
                <label></label>
                <input type="text" placeholder="">
            </section>
            <!--  -->
            <section class="tel">
                <label></label>
                <input type="tel" placeholder="">
            </section>
            <!--  -->
            <section class="postal-code">
                <label></label>
                <input type="text" placeholder="">
            </section>
            <!--  -->
            <section class="address">
                <!--  -->
                <select name="province">
                    <option value="" selected></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <!--  -->
                <select name="city">
                    <option value="" selected></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <!--  -->
                <select name="area">
                    <option value="" selected></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
            </section>
            <!--  -->
            <section class="detail-address">
                <label></label>
                <input type="text" placeholder="">
            </section>
            <!--  -->
            <Button :isSureAddress="true" :text="text"></Button>
        </section>
    </div>
</template>

<script>
// 
import Button from '../../components/button'
export default {
    name: "Address",
    data() {
        return {
            text:""
        }
    },
    components: {
        Button
    }
}
</script>

<style lang="less" scoped>


.mine-datas {
    text-align: left;
    section {
        width: 100%;
        height: 50px;
        line-height: 50px;
        padding: 0 15px;
        box-sizing: border-box;
        border-bottom: 1px solid #ccc;
        background: #fff;

        label {
            display: inline-block;
            width: 20%;
            color: #999;
        }

        input {
            color: #333;
            width: calc(100% - 22%);
        }

        select {
            width: 25%;
            margin-right: 5%;
        }
    }

}
</style>




