import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        title: "",
        // 
        isMain: true,
        //  
        prevIcon: false,
        // 
        isHaveBar: true,
        // 
        isDetail: false,
        //  
        isOnline: false,
        // 
        isAccount: false,
        // 
        isCollection: false,
        // 
        isOrder: false
    },
    mutations: {
        changeTitle(state,title){
            state.title = title;
        },
        isMain(state,isMain){
            state.isMain = isMain;
        },
        addBackIcon(state,isAdd) {
            state.prevIcon = isAdd;
        },
        haveBar(state,isHave) {
            state.isHaveBar = isHave;
        },
        Detail(state,isDetail){
            state.isDetail = isDetail;
        },
        Online(state,flag) {
            state.isOnline = flag;
        },
        Account(state,isAccount) {
            state.isAccount = isAccount;
        },
        Collection(state,isCollection) {
            state.isCollection = isCollection;
        },
        inOrder(state,isOrder) {
            state.isOrder = isOrder;
        }
    }
});


